.project-pagination {
  width: 100%;
  height: 32px;
  display: inline-flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}

button[type="button"] {
  width: 24px;
  height: 24px;
  outline: 0;
  border: 0;
  font-size: 16px;

  color: #F2F2F2;
  background: transparent;
}

button[type="button"]:hover {
  background: #F23D5E;
  color: #F2F2F2;
}

.pink {
  color: #F23D5E;
}